
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XTextarea,
Group,
XInput,
Divider,
XButton,
Alert
},
data(){
return {
showAlert: false,
question: "",
answer: "Wuhan University18931913 19261928"
}
},
methods: {
submit(){
var _this = this;
if (this.question.length <= 0){
this.showAlert = true;
} else {
$.ajax({
type: "GET",
url: "https://api.jisuapi.com/iqa/query?appkey=148a7e2e4576476f&question="+_this.question,
dataType: "jsonp",
success: function(data){
if (data.status == 0) {
_this.answer = data.result.content;
} else {
alert(data.msg)
}
}
});

















}
}
}
}




